import usercenterItem from '../../lib/usercenter_item/userCenterItem';

console.log( usercenterItem )
export default {
    data() {
        return {
            menuList : [
                {
                    image:'me_wallet',
                    title:'钱包'
                },
                {
                    image:'me_record',
                    title:'消费充值记录'
                },
                {
                    image:'me_buy',
                    title:'购买的书'
                },
                {
                    image:'me_vip',
                    title:'我的会员'
                }
            ]
        };
    },
    /**
     * 生命周期函数--监听页面加载
     */
    onLoad: function(options) {
        uni.setNavigationBarTitle({
            title: '我的(待完善)'
        });
    },
    components: {
        usercenterItem
    },
    props: {},
    methods: {
        bindItemClick() {
            uni.showToast({
                title: '暂未开放',
                icon: 'none'
            });
        },

        setData: function(obj) {
            let that = this;
            let keys = [];
            let val, data;
            Object.keys(obj).forEach(function(key) {
                keys = key.split('.');
                val = obj[key];
                data = that.$data;
                keys.forEach(function(key2, index) {
                    if (index + 1 == keys.length) {
                        that.$set(data, key2, val);
                    } else {
                        if (!data[key2]) {
                            that.$set(data, key2, {});
                        }
                    }

                    data = data[key2];
                });
            });
        }
    }
};
